.dotBar{
  position: absolute;
  display: flex;
  flex-direction: column;
  height: 12rem;
  width: 5rem;
  right: 10%;
  z-index: 6;
  align-items: center;
  justify-content: space-evenly;
}

.dot{
  width: 0.5rem;
  height: 2rem;
  border-radius: 0.75rem;
  background-color: var(--light);
  filter: opacity(0.2);
  transform: scale(1) rotate(0deg);
  z-index: 2;
  transition: 0.6s;
}

.dotSelected{
  background-color: var(--light);
  filter: opacity(1) drop-shadow(0px 0px 10px var(--light));
  transform: scale(1) rotate(0deg);
  z-index: 2;
  margin-right: 0rem;
}